var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return (_vm.refresh)?_c('el-card',{staticClass:"box-card",attrs:{"shadow":"never","body-style":{padding:'15px',minHeight:'calc(100vh - 152px)'}}},[_c('vel_md_editor',{attrs:{"content":"\n### 说明\n\nhttp 请求使用 axios 进行的封装，详细配置在 `utils/httpUtil.js` 中，只做了 get、post 的封装，可自行拓展。\n\n### 拦截器\n\n##### 在请求前进行拦截，主要做的功能是前端验证登陆状态及携带 Token 参数。\n\n``` js\naxios.interceptors.request.use(function (config) {\n    // 在发送请求之前做些什么\n    if (config.url.indexOf(LOGIN_API) == -1) {\n        // 如果不是登录请求，需要验证登录是否过期\n        if (isLogin()) {\n            // 请求携带 Token 参数\n            let token = cookieUtil.get(TOKEN)\n            if (config.method.toLowerCase() === \"get\") {\n                if (!config.params) {\n                    config.params = {}\n                }\n                config.params.token = token\n            } else {\n                if (!config.data) {\n                    config.data = {}\n                } else {\n                    config.data = qs.parse(config.data)\n                }\n                config.data.token = token\n                config.data = qs.stringify(config.data)\n            }\n        } else {\n            logout()\n        }\n    }\n    return config;\n}\n```\n\n##### 对全局响应的拦截，主要根据返回的状态码，如服务端登陆失效、权限不足的业务处理。\n\n``` js\n// 添加响应拦截器\naxios.interceptors.response.use(function (response) {\n    // 对响应数据做点什么\n    let res = response.data;\n    if (res.statusCode === STATUS_CODE.NO_LOGIN) {\n        // 服务器返回未登陆，跳转登录页面\n        logout()\n    } else if (res.statusCode === STATUS_CODE.NO_PERM) {\n        Message.warning(\"没有权限，请联系管理员！\")\n    }\n    return response;\n}, function (error) {\n    // 对响应错误做点什么\n    return Promise.reject(error);\n});\n```\n\n### API 配置\n\napi 接口的配置，我放在了 `config/api.js` 中，因为脚手架是模拟的请求，所以请按实际业务进行配置。\n\n### 响应数据的规范\n\n拿登陆成功的例子来讲，json 数据结构应如下：\n\n``` json\n{\n    \"statusCode\": 200,\n    \"message\": \"登陆成功\",\n    \"data\": {\n        \"userName\": \"Yanglinen\",\n        \"token\": \"qwertyuiopasdfghjklzxcvbnm\"\n    }\n}\n```\n\n可参考 `public/static/res/` 文件夹下的 json 文件。\n\n|字段|类型|必须|备注|\n|-|-|-|-|\n|statusCode|整形|是|api 结果状态码|\n|message|字符串|否|api 结果描述，可返回一些提示信息|\n|data|JSONObject|否|api 业务数据包，根据实际业务进行返回|\n\n\n"}})],1):_vm._e()}
